import { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import React from "react";
import { useNavigate } from "react-router-dom";
import * as ROUTS from "../constants/routs";
import { getAuth, signInWithEmailAndPassword } from "firebase/auth";
export default function login() {
  const [email, setEmail] = useState("");
  const [Password, setPassword] = useState("");
  const [error, seterror] = useState("");

  const onhistory = useNavigate();
  const Invalid = email == "" || Password == "";

  const handlelogin = async (event) => {
    event.preventDefault();
    const auth = getAuth();
    try {
      await signInWithEmailAndPassword(auth, email, Password);

      onhistory(ROUTS.DASHBOARD);
    } catch (error) {
      setEmail("");
      setPassword("");
      seterror(error.message);
    }
  };

  useEffect(() => {
    document.title = "LogIn-Instgrame";
  }, []);

  return (
    <div className="container flex mx-auto max-w-screen-md items-center h-screen">
      <div className="sm:flex hidden w-3/5">
        <img src="/images/iphone-with-profile.jpg" alt="iphone with profile" />
      </div>
      <div className=" m-auto sm:m-0 flex  sm:w-2/5 w-10/12  flex-col ">
        <div className="flex flex-col border border-gray-primary bg-white justify-center items-center mb-4 p-4">
          <h1 className="flex justify-center w-full">
            <img
              src="/images/logo.png"
              alt="logo"
              className="mt-2 w-6/12 mb-4"
            />
          </h1>

          {error && <p className="mb-4 text-xs text-red-primary">{error}</p>}
          <form onSubmit={handlelogin} method="POST">
            <input
              type="text"
              aria-label="Enter your Email"
              placeholder="enter email"
              className="text-sm text-gray-base py-5 px-4 mr-3 border w-full h-2 border-gray-primary mb-2 rounded"
              onChange={({ target }) => setEmail(target.value)}
              value={email}
            />

            <input
              aria-label="Enter your password"
              type="password"
              value={Password}
              placeholder="enter password"
              className="text-sm text-gray-base py-5 px-4 border mr-3  w-full h-2 border-gray-primary mb-2 rounded "
              onChange={({ target }) => setPassword(target.value)}
            />
            <button
              disabled={Invalid}
              className={`bg-blue-medium text-white rounded w-full h-8 font-bold ${
                Invalid && "opacity-50 "
              }`}
            >
              Log In
            </button>
          </form>
        </div>
        <div className="flex justify-center items-center flex-col w-full bg-white rounded p-4 border border-gray-primary">
          <p className="text-sm ">
            {" "}
            do you have acount?{" "}
            <Link to="/signup" className="font-blod text-blue-medium">
              Sign up
            </Link>
          </p>
        </div>
      </div>
    </div>
  );
}
